.actions {
  margin-left: auto;
  max-width: 111px;
  padding: 0 11px 0 16px;

  &__list {
    display: flex;
    gap: 16px;
  }

  &__btn {
    border: none;
    outline: none;
    padding: 0;
    cursor: pointer;
    width: 24px;
    height: 24px;
    background-color: var(--dark-gray);
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    &:active {
      background-color: var(--green);
    }

    &--personal {
      &:after {
        content: url("data:image/svg+xml;charset=UTF-8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' width='10' height='7' viewBox='0 0 10 7' fill='none'> <path d='M1 1.07886L5 5L9 1' stroke = '%238E8E8E' stroke-width='2' stroke-linecap='round'/> </svg>");
        width: 16px;
        height: 16px;
        position: absolute;
        left: 30px;
        top: 4px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &:active {
        &:after {
          transform: rotate(180deg);
          content: url("data:image/svg+xml;charset=UTF-8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' width='10' height='7' viewBox='0 0 10 7' fill='none'> <path d='M1 1.07886L5 5L9 1' stroke = '%2330BF60' stroke-width='2' stroke-linecap='round'/> </svg>");
        }
      }
    }

    &--notifications {

      position: relative;

      &:after {
        position: absolute;
        width: 20px;
        height: 20px;
        right: -8px;
        top: -8px;
        background-color: var(--red);
        border: 1px solid var(--white);
        content: '5';
        border-radius: 50%;
        color: var(--white);
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &:active {
        .modal-notifications {
          transform: scaleY(1) scaleX(1) translateY(-16px) translateX(0px);
        }
      }
    }
  }
}
